const products = require('./products')

// ---------------------------
// Analyze text
// ---------------------------
function breakComma(sentence) {
  return sentence.split(',')
}

function normalize(string) {
  const result = string.normalize('NFD').replace(/[\u0300-\u036f]/g, '')
  return result
}

function breakSentence(sentence) {
  return normalize(sentence)
    .replace(/\n|vs|voi|va/g, '$&,')
    .replace(/\d+\s*me/g, '$&,')
    .replace(/\d+\s*muoi/g, '$&,')
    .replace(/\d+\s*bo/g, '$&,')
    .replace(/\d+\s*kho ga/g, '$&,')
    .replace(/\d+\s*ot tac/g, '$&,')
    .replace(/\d+\s*tac/g, '$&,')
    .replace(/\d+\s*tat/g, '$&,')
    .replace(/\d+\s*sate ot/g, '$&,')
}

function matchCondition(line) {
  return function({ regex }) {
    return line ? line.match(regex) : false
  }
}

function detectProduct(line) {
  // prettier-ignore 
  const testConditions = [
    { regex: /tron muoi ot/, output: 'bt_tron_muoi_ot' },
    { regex: /banh trang me|bt me/, output: 'bt_me' },
    { regex: /banh trang muoi dau|bt muoi|muoi dau/, output: 'bt_muoi_dau' },
    { regex: /sate tac|sate tat|te tat|te tac|sa te tac|sate ot cay|ot cay/, output: 'bt_sa_te_tac' },
    { regex: /tron tac|tron tat|tron tac|bt tron/, output: 'bt_tron_tac' },
    { regex: /banh bo|banh trang bo|bt bo/, output: 'bt_bo' },
    { regex: /toi|banh toi|sate toi|sa te toi|banh trang toi/, output: 'bt_sa_te_toi' },
    { regex: /deo cay|cay deo/, output: 'bt_deo_cay' },
    { regex: /deo me|me deo/, output: 'bt_deo_me' },
    { regex: /deo tom|tom deo/, output: 'bt_deo_tom' },
    { regex: /hop muoi|hu muoi|hu muoi|muoi tom/, output: 'muoi_tom' },
    { regex: /muoi chay|muoi ot/, output: 'muoi_chay' },
    { regex: /cha bong/, output: 'bt_cha_bong' },
    { regex: /kho ga/, output: 'kho_ga' },
    { regex: /phoi suong|banh trang ps/, output: 'bt_phoi_suong' },
    { regex: /chum ruot|chum ruoc/, output: 'chum_ruot' },
    { regex: /mut me|muc me|me ngao/, output: 'mut_me' },
    { regex: /banh trang vuong/, output: 'bt_vuong' },
    { regex: /ong tom/, output: 'bt_ong_tom' },

    // Default for duplicated regex
    { regex: /me/, output: 'bt_me' },
    { regex: /bo/, output: 'bt_bo' },
    { regex: /muoi/, output: 'bt_muoi_dau' },
    { regex: /tac|tat/, output: 'bt_sa_te_tac' },
    { regex: /tron/, output: 'bt_tron_tac' },
    { regex: /sate|sa te/, output: 'bt_sa_te_tac' }
  ]

  const result = testConditions.find(matchCondition(line.toLowerCase()))

  if (result) {
    return products[result.output]
  }

  return null
}

function detectQuantity(line) {
  const matches = line.match(/\d+/g)
  return matches != null ? parseFloat(matches[0]) : 0
}

function detectProductAndQuantity(line) {
  const product = detectProduct(line)
  const quantity = detectQuantity(line)

  return {
    product: product,
    quantity: quantity
  }
}

function undetectedItem({ product }) {
  return !!product
}

function sentenceToItems(sentence) {
  return breakComma(breakSentence(sentence))
    .map(detectProductAndQuantity)
    .filter(undetectedItem)
}

module.exports = {
  detectQuantity,
  detectProduct,
  sentenceToItems
}
